<template>
  <el-card class="">
    <div slot="header">
      <span>权限设置</span>
    </div>
    <el-steps :active="current_step" finish-status="success" align-center>
      <el-step title="路由权限"></el-step>
      <el-step title="菜单、操作权限"></el-step>
    </el-steps>
    <!-- 第一步：设置路由 -->
    <div v-show="current_step === 0" class="route-panel">
      <el-tree :data="routes_tree" ref="route_tree" :check-strictly="route_check_strictly"
               show-checkbox highlight-current default-expand-all node-key="route_id">
        <div class="custom-tree-node" slot-scope="{ node, data }">
          <el-tooltip class="item" effect="dark" :content="data.route_name" placement="right">
            <span>{{ data.permission_remark }}</span>
          </el-tooltip>
        </div>
      </el-tree>
    </div>
    <!-- 第二步：设置菜单/操作按钮 -->
    <div class="menu-panel" v-show="current_step === 1">
      <el-tree :data="menus_tree" ref="menu_tree" :check-strictly="menu_check_strictly"
               show-checkbox highlight-current default-expand-all node-key="permission_id">
        <div class="custom-tree-node" slot-scope="{ node, data }">
          <!-- 菜单 -->
          <div class="node-menu" v-if="data.resource_type_id === 2">
            <i class="el-icon-notebook-2"></i>
            {{ data.menu_name }}
          </div>
          <!-- 操作按钮 -->
          <div class="node-operation" v-if="data.resource_type_id === 3">
            <i class="el-icon-s-operation"></i>
            {{ data.permission_remark }}
          </div>
        </div>
      </el-tree>
    </div>
    <!-- 操作按钮 -->
    <div class="action-section am-padding-vertical">
      <!-- 第一步：设置路由 -->
      <el-button @click="handleSetRoleRoute" v-show="current_step === 0" type="primary" size="medium">下一步</el-button>
      <el-button @click="$router.back()" v-show="current_step === 0" plain size="medium">取消</el-button>
      <!-- 第二步：设置菜单/操作按钮 -->
      <el-button @click="current_step = 0" v-show="current_step === 1" plain size="medium">上一步</el-button>
      <el-button v-show="current_step === 1" type="success" size="medium">保存设置</el-button>
    </div>
  </el-card>
</template>

<script>
import { mapGetters } from 'vuex'
import { Role } from '@/api/'

export default {
  name: "Setting",
  props: ['role_id'],
  data() {
    return {
      // 步骤条
      current_step: 0,
      // 路由--是否严格的遵循父子不互相关联
      route_check_strictly: true,
      // 路由--选中的路由 permission_id 数组
      route_checked_keys: [],
      // 菜单--是否严格的遵循父子不互相关联
      menu_check_strictly: true,
      // 菜单--选中的菜单 permission_id 数组
      menu_checked_keys: [],
    }
  },
  computed: {
    // 加载所有路由权限
    ...mapGetters('Route', ['routes_tree']),
    // 加载所有菜单、操作权限
    ...mapGetters('Menu', ['menus_tree']),
  },
  watch: {
    route_checked_keys: {
      handler(newValue, oldValue) {
        console.log(newValue)
      },
      // 深层侦听
      deep: true,
    }
  },
  created() {
    // 设置 title
    document.title = '设置角色权限';
    // 还原角色之前设置的路由
    this.handleSetCheckedRoute();
  },
  methods: {
    // 还原角色之前设置的路由
    async handleSetCheckedRoute() {
      // 根据角色加载对应路由
      let { status, data } = await Role.route({ role_id: this.role_id, type: 'flat' });
      if (status) {
        // 转化为选中的路由id
        let checked_keys = data.map((route) => route.route_id);
        // 设置选中的路由
        this.$refs.route_tree.setCheckedKeys(checked_keys);
        // 还原选中状态之后，设置父子节点互相关联
        this.route_check_strictly = false;
      }
    },
    // 设置角色路由权限
    handleSetRoleRoute() {
      // 获取当前选中的路由 node节点（包含半选中父级节点）
      let route_checked_nodes = this.$refs.route_tree.getCheckedNodes(false, true);
      // 转化为 permission_id 数组
      this.route_checked_keys = route_checked_nodes.map((route) => route.permission_id);
      // 切换步骤条
      this.current_step = 1;
    },
    // 设置选中的菜单
    async handleSetCheckedMenu() {
      // 根据角色加载对应菜单
      let { status, data } = await Role.menu({ role_id: this.role_id, type: 'flat' });
      if (status) {
        // 转化为选中的菜单id
        let checked_keys = data.map((menu) => menu.id);
        // 设置选中的菜单
        this.$refs.route_tree.setCheckedKeys(checked_keys);
        // 还原选中状态之后，设置父子节点互相关联
        this.menu_check_strictly = false;
      }
    },
    // 保存菜单设置
    async handleSetRoleMenu() {
      // 获取当前选中的node节点（包含半选中父级节点）
      let checked_nodes = this.$refs.tree.getCheckedNodes(false, true);
      // 转化为id数组
      let checked_keys = checked_nodes.map((menu) => menu.id);
      // 发送菜单配置
      let { status, msg } = await Role.setMenu({ id: this.current_role_id, menus: checked_keys });
      if (status) {
        this.$message.success(msg);
        // 关闭drawer
        this.setting_visible = false;
      } else {
        this.$message.error(msg);
      }
    },
  }
}
</script>

<style lang="less" scoped>
@blue: #409EFF;
@green: #67C23A;
@orange: #E6A23C;

.route-panel, .menu-panel {
  margin-top: 20px;
  padding: 20px;
  border-radius: 3px;
}

.route-panel {
  border: 1px solid @blue;
}

.menu-panel {
  border: 1px solid @green;

  .node-menu i {
    color: @green;
  }

  .node-operation i {
    color: @orange;
  }
}

.custom-tree-node {
  flex: 1;
  display: flex;
  align-items: center;
  font-size: 14px;
  padding-right: 8px;
}
</style>